.layout-ribbon {
  width: 100%;
  height: 60vh;
  background: #99ccff;
  /* For browsers that do not support gradients */
  background: -webkit-linear-gradient(left top, #ff6699, #ffccdc);
  /* For Safari 5.1 to 6.0 */
  background: -o-linear-gradient(bottom right, #ff6699, #ffccdc);
  /* For Opera 11.1 to 12.0 */
  /* For Firefox 3.6 to 15 */
  background: -webkit-gradient(linear, left top, right bottom, from(#ff6699), to(#ffccdc));
  background: -webkit-linear-gradient(top left, #ff6699, #ffccdc);
  background: -o-linear-gradient(top left, #ff6699, #ffccdc);
  background: linear-gradient(to bottom right, #ff6699, #ffccdc);
  /* Standard syntax */
  -ms-flex-negative: 0;
  flex-shrink: 0; }

.layout-app {
  font-family: 'Roboto', 'Helvetica', 'Arial', sans-serif !important; }



